// Contact.js
import React from 'react';

const Contact = () => {
  return (
    <div id="contact" style={{ minHeight: '100vh', padding: '20px', display: 'flex', flexDirection: 'column' }}>
      <h2>Contact Us</h2>
      <form style={{ maxWidth: '400px', margin: '0 auto' }}>
        <div style={{ marginBottom: '15px' }}>
          <label htmlFor="name">Name:</label>
          <input type="text" id="name" name="name" placeholder="Your Name" style={{ width: '100%' }} />
        </div>

        <div style={{ marginBottom: '15px' }}>
          <label htmlFor="email">Email:</label>
          <input type="email" id="email" name="email" placeholder="Your Email" style={{ width: '100%' }} />
        </div>

        <div style={{ marginBottom: '15px' }}>
          <label htmlFor="message">Message:</label>
          <textarea id="message" name="message" placeholder="Your Message" style={{ width: '100%' }} />
        </div>

        <button type="submit" style={{ backgroundColor: '#4CAF50', color: 'white', padding: '10px 15px', border: 'none', borderRadius: '5px', cursor: 'pointer' }}>
          Submit
        </button>
      </form>
    </div>
  );
};

export default Contact;
