import React from 'react';

const PerkCard = ({ title, description }) => (
  <div className="card" style={{ flex: '1', maxWidth: '18rem', margin: '10px' }}>
    <div className="card-body">
      <h2 className="card-title">{title}</h2>
      <p className="card-text">{description}</p>
    </div>
  </div>
);

const PrefacePage = () => {
  return (
    <div style={{ marginTop: '8vh', display: 'flex', flexDirection: 'column', alignItems: 'center' }} id="preface">
      <h1 className="text-center">Preface Page</h1>
      <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}>
        {/* Perk Card 1 */}
        <PerkCard
          title="Competitive Salaries"
          description="We offer competitive salaries to ensure that your hard work is rewarded."
        />

        {/* Perk Card 2 */}
        <PerkCard
          title="Flexible Work Environment"
          description="Enjoy a flexible work environment that promotes work-life balance."
        />

        {/* Perk Card 3 */}
        <PerkCard
          title="Professional Development"
          description="Invest in your professional growth with our ongoing training and development programs."
        />
      </div>
    </div>
  );
};

export default PrefacePage;
