import React from 'react';
import { Link } from 'react-scroll';

const NavBar = () => {
  return (
    <nav className="bg-blue-500 bg-opacity-80 text-white fixed top-0 left-0 w-full z-50">
      <div className="container mx-auto flex items-center justify-between py-4">
        <Link to="home" smooth={true} duration={500} className="text-lg font-bold">
          Dabster
        </Link>
        <button
          className="lg:hidden px-3 py-2 text-xl focus:outline-none"
          data-toggle="collapse"
          data-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="text-white">&#9776;</span>
        </button>
        <div className="hidden lg:flex lg:items-center lg:w-auto" id="navbarNav">
          <ul className="flex flex-col lg:flex-row list-none lg:ml-auto">
            <li className="nav-item">
              <Link to="home" smooth={true} duration={500} className="nav-link px-3 py-2 text-white hover:text-gray-300">
                Home
              </Link>
            </li>
            <li className="nav-item">
              <Link to="preface" smooth={true} duration={500} className="nav-link px-3 py-2 text-white hover:text-gray-300">
                Preface
              </Link>
            </li>
            <li className="nav-item">
              <Link to="about" smooth={true} duration={500} className="nav-link px-3 py-2 text-white hover:text-gray-300">
                About
              </Link>
            </li>
            <li className="nav-item">
              <Link to="contact" smooth={true} duration={500} className="nav-link px-3 py-2 text-white hover:text-gray-300">
                Contact
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default NavBar;
