// Home.js
import React from 'react';
import About from './About';  // Import the About component
import Contact from './Contact';  // Import the Contact component
import ImageSlider from './ImageSlider';
import PrefacePage from './PrefacePage';
const Home = () => {
  // List of images for the carousel
  const slides = [
    'https://images.unsplash.com/photo-1531297484001-80022131f5a1?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2620&q=80',
    'https://images.unsplash.com/photo-1488590528505-98d2b5aba04b?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2670&q=80',
    'https://images.unsplash.com/photo-1661961112951-f2bfd1f253ce?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2672&q=80',
    'https://images.unsplash.com/photo-1512756290469-ec264b7fbf87?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2253&q=80',
    'https://images.unsplash.com/photo-1552872673-9b7b99711ebb',
    'https://images.unsplash.com/photo-1519575706483-221027bfbb31',
    'https://images.unsplash.com/photo-1511227499331-25c621db940e'
  ];


  return (
    <div>
     {/* <ImageCarousel images={carouselImages} />*/}
     <div className='flex justify-center items-center gap-2 '>
     <ImageSlider urls={slides} />
        </div>
    
      {/* Home Page Content */}
      <div style={{ minHeight: '100vh', display: 'flex', flexDirection: 'column' }}>
        <h2>Welcome to our Quantum World!</h2>
        <p>
          In the enigmatic landscape of quantum mechanics, where particles dance between possibilities,
          we invite you to explore the intricate tapestry of our Quantum World. Here, the ordinary transforms into the extraordinary.
        </p>

        <p>
          Immerse yourself in the captivating phenomena of superposition and quantum entanglement.
          Witness particles defy classical expectations, transcending the boundaries of the known and venturing into the unexplored.
        </p>

        <p>
          The Quantum World beckons, where observation becomes an art form, and the universe unfolds in ways beyond imagination.
          Join us on this cosmic odyssey as we delve into the mysteries that shape the very fabric of reality.
        </p>

        {/* Iron Man Inspired Content */}
        <h2>Iron Man Meets Quantum Realms</h2>
        <p>
          Inspired by the genius of Tony Stark and the technological marvels of the Iron Man suit,
          our Quantum World is not only a journey into the microscopic, but it's also a fusion of advanced technology and cosmic wonders.
        </p>

        <p>
          Picture a quantum suit that harnesses the power of entangled particles, allowing us to navigate through the fabric of spacetime
          with the same flair as Iron Man soaring through the skies. The intersection of science and fiction brings forth limitless possibilities.
        </p>

        <p>
          As we explore the Quantum Realms, we draw inspiration from the marvels of Stark Industries and the audacity of Tony Stark.
          Just as Iron Man reshaped the landscape of superheroes, we aim to reshape the landscape of interstellar exploration.
        </p>
      </div>

      {/* About Page Content */}
      <About />
      <div style={{ padding: '20px' }}>
        <PrefacePage />
      </div>
      {/* Contact Page Content */}
      <div style={{ padding: '20px' }}>
        <Contact />
      </div>
    </div>
  );
};

export default Home;
