// About.js
import React from 'react';

const About = () => {
  return (
    <div id="about" style={{ minHeight: '100vh', padding: '20px', display: 'flex', flexDirection: 'column' }}>
      <h1>Welcome to the Interspace Travel Authority</h1>
      <p>
        At the Interspace Travel Authority, we embark on a cosmic journey beyond the boundaries of our home planet.
        Our mission is to push the frontiers of space exploration and make interstellar travel a reality for future generations.
      </p>

      <p>
        Equipped with cutting-edge propulsion systems and warp-speed technology, our spacecraft defy the limits of conventional travel.
        Imagine traversing the vastness of the cosmos, discovering new galaxies, and exploring celestial wonders previously unseen by human eyes.
      </p>

      <p>
        Our team of visionary scientists, engineers, and adventurers is dedicated to unlocking the mysteries of the universe.
        From wormholes to antimatter propulsion, we're at the forefront of groundbreaking research that will propel humanity into a new era of space exploration.
      </p>

      <p>
        Join us as we push the boundaries of possibility and embark on a journey that transcends the realms of imagination.
        The universe awaits, and the Interspace Travel Authority is your gateway to the extraordinary.
      </p>
    </div>
  );
};

export default About;
